  .search-box {
    width: 100%;
    margin: 0 0 75px;
  }

  .search-box .search-bar {
    display: inline-block;
    width: 60%;
    padding: 15px;
    appearance: none;
    background: none;
    border: none;
    outline: none;
    background-color: rgba(255, 255, 255, 0.5);
    /* border-radius: 0px 0px 16px 16px; */
    border-radius: 10px;
    margin-top: -5px;
    box-shadow: 0px 5px rgba(0, 0, 0, 0.2);
    color: #313131;
    font-size: 20px;
    transition: 0.4s ease;
  }

  .search-box .search-bar:focus {
    background-color: rgba(255, 255, 255, 0.75);
  }

  .location-box .location {
    margin-top: 80px;
    color: white;
    font-size: 55px;
    font-weight: 500;
    text-align: center;
    text-shadow: 3px 3px rgba(50, 50, 70, 0.5);
  }

  .location-box .date {
    margin-top: 20px;
    color: white;
    font-size: 25px;
    font-weight: 300;
    font-style: italic;
    text-align: center;
    text-shadow: 2px 2px rgba(50, 50, 70, 0.5);
  }

  .weather-box {
    text-align: center;
    color: white;
  }

  .weather-box .temp {
    position: relative;
    display: inline-block;
    margin: 30px auto;
    background-color: rgba(255, 255, 255, 0.2);
    border-radius: 16px;
    padding: 15px 25px;
    color: white;
    font-size: 90px;
    font-weight: 900;
    text-shadow: 3px 6px rgba(50, 50, 70, 0.5);
    text-align: center;
    box-shadow: 3px 6px rgba(0, 0, 0, 0.2);
  }

  .weather-box .weather {
    color: white;
    font-size: 30px;
    font-weight: 700;
    text-shadow: 3px 3px rgba(50, 50, 70, 0.5)
  }